import { container } from '../css-modules/CoinContainer.module.css';

import BitcoinLogo from '../static/svg/btc.png';
import EthereumLogo from '../static/svg/eth.png';
import StellarLogo from '../static/svg/xlm.png';
import XrpLogo from '../static/svg/xrp.png';
import DogeLogo from '../static/svg/doge.png';

const CoinContainer = () => {
  const handleDragStart = (e) => {
    e.dataTransfer.setData('coin', e.target.dataset.coin);
  };

  return (
    <>
      <div className={container}>
        <img
          draggable={true}
          src={BitcoinLogo}
          width={50}
          alt='Bitcoin Logo'
          onDragStart={handleDragStart}
          data-coin='bitcoin'
        />
        <img
          draggable={true}
          src={EthereumLogo}
          width={50}
          alt='Eth Logo'
          onDragStart={handleDragStart}
          data-coin='ethereum'
        />
        <img
          draggable={true}
          src={StellarLogo}
          width={50}
          alt='Stellar Logo'
          onDragStart={handleDragStart}
          data-coin='stellar'
        />
        <img
          draggable={true}
          src={XrpLogo}
          width={50}
          alt='Ripple Logo'
          onDragStart={handleDragStart}
          data-coin='ripple'
        />
        <img
          draggable={true}
          src={DogeLogo}
          width={50}
          alt='Dogecoin Logo'
          onDragStart={handleDragStart}
          data-coin='dogecoin'
        />
      </div>
    </>
  );
};

export default CoinContainer;
