import { UPDATE_CRYPTO } from './crypto.types';

const initialState = {
  name: null,
  description: null,
  images: [],
  homepageUrl: null,
  currentPriceUsd: null,
  marketCapUsd: null,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_CRYPTO:
      return {
        ...state,
        name: action.payload.name,
        description: action.payload.description,
        images: action.payload.images,
        homepageUrl: action.payload.homepageUrl,
        currentPriceUsd: action.payload.currentPriceUsd,
        marketCapUsd: action.payload.marketCapUsd,
      };

    default:
      return state;
  }
};

export default reducer;
