import { updateCrypto } from '../redux/crypto/crypto.actions';
import { connect } from 'react-redux';
import { container } from '../css-modules/LandingPad.module.css';
import CurrencyFormatter from 'currency-formatter';

function createMarkup(mu) {
  return {
    __html: mu,
  };
}

const NoResults = () => {
  return (
    <div>Please drag a crypto coin into this container to return data.</div>
  );
};

const DisplayCryptoResults = (props) => {
  return (
    <>
      {/* Output API Results */}
      <img src={props.images.thumb} alt={props.name} />
      <h3>Name: {props.name}</h3>
      <p dangerouslySetInnerHTML={createMarkup(props.description)}></p>
      <p>Homepage URL: {props.homepageUrl}</p>
      <p>
        Current Price:{' '}
        {CurrencyFormatter.format(props.currentPriceUsd, { code: 'USD' })}
      </p>
      <p>
        Market Cap:{' '}
        {CurrencyFormatter.format(props.marketCapUsd, { code: 'USD' })}
      </p>
    </>
  );
};

const handleDragOver = (e) => {
  e.stopPropagation();
  e.preventDefault();
};

const handleDrop = async (e, updateCrypto) => {
  const coin = e.dataTransfer.getData('coin');

  const response = await fetch(`https://api.coingecko.com/api/v3/coins/${coin}`)
    .then(function (response) {
      return response.json();
    })
    .then(function (data) {
      return data;
    })
    .catch(function (err) {
      console.warn('Something went wrong.', err);
    });

  updateCrypto({
    name: response.name,
    description: response.description.en,
    images: response.image,
    homepageUrl: response.links.homepage[0],
    currentPriceUsd: response.market_data.current_price.usd,
    marketCapUsd: response.market_data.market_cap.usd,
  });
};

const LandingPad = (props) => {
  const {
    name,
    description,
    images,
    homepageUrl,
    currentPriceUsd,
    marketCapUsd,
  } = props;

  return (
    <>
      <div
        className={container}
        onDrop={(e) => {
          handleDrop(e, props.updateCrypto);
        }}
        onDragOver={handleDragOver}
      >
        {name === null ? (
          <NoResults />
        ) : (
          <DisplayCryptoResults
            name={name}
            description={description}
            homepageUrl={homepageUrl}
            currentPriceUsd={currentPriceUsd}
            marketCapUsd={marketCapUsd}
            images={images}
          />
        )}
      </div>
    </>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCrypto: (coin) => dispatch(updateCrypto(coin)),
  };
};

const mapStateToProps = (state) => {
  return state.crypto;
};

export default connect(mapStateToProps, mapDispatchToProps)(LandingPad);
